body {
  font-family: sans-serif;
}
.block {
  height: 50px;
  width: 50px;
  background-color: gray;
  font-size: 45px;
  color: white;
  text-shadow: black 2px 2px;
  text-align: center;
  border-radius: 3px;
  margin-right: 5px;
  float: left;
}
.yellow {
  background-color: yellow;
}
.green {
  background-color: green;
}
button {
  background-color: transparent;
  border: none;
  color: blue;
  text-decoration: underline;
  cursor: pointer;
}
canvas {
  background-color: black;
  height: 50vw;
  width: 50vw;
  position: absolute;
  top: calc(50vh - 25vw);
  right: 0;
}
.half {
  background-color: rgb(29, 29, 29);
  height: 100vh;
  width: 50vw;
  position: absolute;
  top: 0;
  right: 0;
}
#points {
  right: 10px;
  top: 10px;
  position: absolute;
  color: white;
  z-index: 10000;
}
